@font-face{
    font-family: RobotoCondensed;
    src: url(assets/roboto-condensed.light.ttf);
    font-weight: 800;
}

@font-face{
    font-family: EconomicaBoldss;
    src: url(assets/economica-bold.ttf);
}

body{
    text-align: center;
    font-size: 20px;
    padding: 2.5%;
}

h1, h2, h3, h4, h5 {
    font-family: 'EconomicaBoldss';
}

p {
    font-family: 'RobotoCondensed';

}

.profile {
    width: 200px;
    height: 200px;
    border-radius: 100px;
}

.title-fade-in {
    opacity: 100;
    animation-name: fade-in;
    animation-duration: 2s;
}

.title-fade-out {
    opacity: 0;
    animation-name: fade-out;
    animation-duration: 2s;
}

.headerLink {
    display: inline-block;
    margin: 10px;
    margin-bottom: 30px;
}

@keyframes fade-out {
    from { opacity: 100 }
    to { opacity: 0 }
}

@keyframes fade-in {
    from { opacity: 0 }
    to { opacity: 100 }
}